<template>
  <div class="login auth container">
    <h1 class="auth__title">Login</h1>

    <form
      class="auth__form"
      @submit.prevent="login"
    >
      <form-row>
        <form-control>
          <input
            autocomplete="email"
            class="form-field"
            name="email"
            placeholder="Please enter your email"
            required
            type="email"
            v-model="form.email"
          >
        </form-control>
      </form-row>

      <form-row>
        <form-control>
          <input
            autocomplete="current-password"
            class="form-field"
            name="password"
            placeholder="Please enter your password"
            required
            type="password"
            v-model="form.password"
          >
        </form-control>
      </form-row>

      <form-row>
        <button
          class="form-button form-button--submit auth__form-submit"
          type="submit"
        >
          Send
        </button>
      </form-row>
    </form>

    <div class="auth__links">
      <router-link
        class="auth__link"
        title="Create an account"
        to="/sign-up"
      >
        Sign up
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import firebase from '@/firebase'
import { FormControl, FormRow } from '@/components/form'

export default {
  name: 'Login',
  components: {
    FormControl,
    FormRow
  },
  data () {
    return {
      form: {
        email: null,
        password: null
      }
    }
  },
  methods: {
    ...mapMutations(['TOGGLE_BASE_MODAL', 'UPDATE_USER']),
    login () {
      const { email, password } = this.form;

      firebase
        .auth()
        .signInWithEmailAndPassword(email, password)
        .then(({ user }) => {
          this.UPDATE_USER(user);
          this.$router.replace('/');
        })
        .catch(err => {
          this.TOGGLE_BASE_MODAL({
            buttonResolve: 'Ok',
            text: err.message,
            title: 'Error'
          });
        });
    }
  }
}
</script>
